var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1'),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        R2-D2，或Artoo-Detoo，是在一个虚构的机器人字符星球大战特许通过创建乔治卢卡斯。作为一个小型的星际机器人，R2-D2是一个主要角色，并且出现在迄今为止的十部星球大战电影中的九部。在整部电影中，R2是C-3PO，PadméAmidala，Anakin Skywalker，Leia Organa，Luke Skywalker的朋友，欧比旺克诺比在传奇的各个方面。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        英国演员肯尼贝克在所有三部原版星球大战电影中都演奏了R2-D2 ，并获得了前传三部曲中角色的计费功能，其中贝克的角色减少了，因为R2-D2主要由无线电控制的道具和CGI模型描绘。在续集三部曲中，贝克被誉为The Force Awakens的顾问; 然而，Jimmy Vee也在一些场景中共同表演了角色。Vee后来接替了The Last Jedi的角色。R2-D2的声音和声音效果是由Ben Burtt创造的。R2-D2由Ralph McQuarrie设计，由John Stears共同开发，由Tony Dyson建造。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        乔治卢卡斯创作的R2-D2受到了黑泽明的1958 年长片“隐藏的堡垒”（美国1962年发行）的影响，特别是Tahei和Matashichi，这两个喜剧角色扮演了Makabe将军的角色。Lucas和艺术家Ralph McQuarrie也从道格拉斯特朗布尔 1972年电影“ 沉默的奔跑 ”中的机器人Huey，Dewey和Louie中汲取灵感。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        这个名字来源于卢卡斯制作他早期的一部电影“ 美国涂鸦”。声音编辑沃尔特·默奇（Walter Murch）表示，他对话语负责，这引发了机器人的名字。Murch以缩写形式“R-2-D-2”要求Reel 2，Dialog Track 2。卢卡斯在房间里，在制作星球大战的剧本时打瞌睡，当他听到这个请求时，他立刻醒了过来，在要求澄清之后说，在回到写剧本之前，这是一个“伟大的名字”。根据电影“星球大战”发行后发布的星球大战百科全书，R2-D2代表第二代机器人Droid系列-2 。特殊效果工作室The White Horse Toy Company的所有者Tony Dyson受委托制作设计，制作了四个由遥控器操作的单元。Baker使用了两个，两个是针对场景的特技双模型，其中机器人从沼泽射击到Dagobah岸边。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"